import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, NavParams } from 'ionic-angular';
import { trigger, state, style, animate, transition } from '@angular/animations';

import { Trip } from '../../models/trip';
import { Trips } from '../../providers/providers';

@IonicPage()
@Component({
  selector: 'page-trip-detail',
  templateUrl: 'trip-detail.html',
  animations: [
    trigger(
      'fadeInOut',
      [
        transition(
          ':enter', [
            style({opacity: 0}),
            animate('250ms', style({'opacity': 1}))
          ]
        ),
        transition(
          ':leave', [
            style({'opacity': 1}),
            animate('250ms', style({'opacity': 0})),
          ]
        )
      ]
    )
  ]
})
export class TripDetailPage {
  trip: Trip;
  itinerary: string;
  private tripDates: any[];

  constructor(public navCtrl: NavController, navParams: NavParams, public modalCtrl: ModalController, public trips: Trips) {
    this.trip = navParams.get('trip') as Trip;

    this.itinerary = '1';

    this.tripDates = this.trip.dates;
  }

  editTrip() {
    console.log('Editing trip: ' + this.trip.name);
  }

  openPlan(plan) {
    console.log("Open: " + JSON.stringify(plan));
    this.navCtrl.push('TripViewPlanPage', {
      plan: plan
    });
  }

  editPlan(plan) {
    console.log("Edit: " + JSON.stringify(plan));

    let addModal = this.modalCtrl.create('TripEditPlanPage', {plan: plan});
    addModal.onDidDismiss(newPlan => {
      if (newPlan) {
        this.trip.updatePlan(newPlan, plan);
      }
    });
    addModal.present();
  }

  deletePlan(plan) {
    console.log("Delete: " + JSON.stringify(plan));
  }
}



